"use client";
import { useState } from "react";
import axios from "axios";
import CodeEditor from "../components/CodeEditor";
import OutputPane from "../components/OutputPane";
import Toolbar from "../components/Toolbar";
import useProctoring from "../hooks/useProctoring";

export default function Home() {
  const [code, setCode] = useState("# Write Python code here\n");
  const [output, setOutput] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const { violations, locked } = useProctoring(3);

  const runCode = async () => {
    setLoading(true);
    setError("");
    try {
      const res = await axios.post("http://localhost:5000/execute", { code });
      setOutput(res.data.output);
      setError(res.data.error);
    } catch {
      setError("Execution failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const downloadPdf = async () => {
    try {
      const res = await axios.post(
        "http://localhost:5000/export-pdf",
        { code, output },
        { responseType: "blob" }
      );
      const url = window.URL.createObjectURL(new Blob([res.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", "code_output.pdf");
      document.body.appendChild(link);
      link.click();
    } catch {
      setError("PDF generation failed.");
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 py-6">
      <div className="max-w-5xl mx-auto space-y-6">
        
        {/* Header */}
        <header className="flex flex-col items-start sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-800">Python Code Editor</h1>
            <p className="text-sm text-gray-500 mt-1">
              Violations: <span className="font-semibold">{violations}</span> / 3
            </p>
          </div>
          {locked && (
            <span className="mt-2 sm:mt-0 text-red-500 font-semibold">
              Session Locked
            </span>
          )}
        </header>

        {/* Editor Section */}
        <section className="bg-white shadow rounded-lg p-4">
          <CodeEditor code={code} setCode={setCode} disabled={locked} />
          <Toolbar
            onRun={runCode}
            onDownload={downloadPdf}
            locked={locked || loading}
          />
          {loading && (
            <p className="text-sm text-blue-500 mt-2">Running code...</p>
          )}
        </section>

        {/* Output Section */}
        <section className="bg-white shadow rounded-lg p-4">
          <h2 className="text-xl font-semibold mb-2 text-gray-800">Output</h2>
          {error && !loading && (
            <div className="bg-red-100 border border-red-300 text-red-700 p-2 rounded mb-2">
              {error}
            </div>
          )}
          <OutputPane output={output} error={error} />
        </section>
      </div>
    </div>
  );
}
