import { useState } from "react";
import Editor from "@monaco-editor/react";

export default function CodeEditor({ code, setCode, disabled }) {
  const [showWarning, setShowWarning] = useState(false);

  const showToast = (message) => {
    setShowWarning(message);
    setTimeout(() => setShowWarning(false), 2000);
  };

  const handleEditorMount = (editor) => {
    editor.onKeyDown((e) => {
      const key = e.code || e.keyCode;
      if (e.ctrlKey || e.metaKey) {
        if (["KeyV", "KeyC", "KeyX"].includes(key)) {
          e.preventDefault();
          showToast("Clipboard actions are disabled!");
        }
      }
    });

    editor.onContextMenu((e) => {
      e.preventDefault();
      showToast("Right-click is disabled!");
    });
  };

  return (
    <div className="relative border border-gray-200 rounded-lg overflow-hidden shadow-sm">
      <div className="bg-gray-50 px-3 py-2 text-sm font-medium text-gray-600 border-b">
        Python Editor
      </div>
      <Editor
        height="400px"
        language="python"
        theme="vs-dark"
        value={code}
        onChange={(value) => setCode(value)}
        options={{
          readOnly: disabled,
          minimap: { enabled: false },
        }}
        onMount={handleEditorMount}
      />
      {showWarning && (
        <div className="absolute top-3 right-3 bg-yellow-400 text-black px-3 py-1 rounded shadow-md text-sm animate-fadeIn">
          {showWarning}
        </div>
      )}
    </div>
  );
}
