export default function OutputPane({ output, error }) {
  return (
    <div className="border border-gray-200 rounded-lg shadow-sm overflow-hidden">
      <div className="bg-gray-50 px-3 py-2 text-sm font-medium text-gray-600 border-b">
        Output
      </div>
      <div className="bg-gray-900 text-white p-4 font-mono text-sm h-40 overflow-auto">
        {error ? (
          <pre className="text-red-400 whitespace-pre-wrap">{error}</pre>
        ) : (
          <pre className="whitespace-pre-wrap">{output}</pre>
        )}
      </div>
    </div>
  );
}
